import React, { useState } from 'react';
import { useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export default function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch('https://backend-userlist-7.onrender.com/user/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, password }),
      });

      const data = await response.json();

      if (response.ok) {
        toast.success('!', { autoClose: 3000 });
        localStorage.setItem('token', data.token);  // Store token in localStorage
        navigate('/');
      } else {
        toast.error(data.message || '', { autoClose: 3000 });
      }
    } catch (err) {
      toast.error('', { autoClose: 3000 });
    }
  };

  return (
    <div className="text-center">
      <h1 className="mb-4"></h1>
      <form onSubmit={handleSubmit}>
        <div className="mb-3 position-relative" style={{ maxWidth: '400px', margin: '0 auto' }}>
          <label className="form-label"></label>
          <input
            type="email"
            className="form-control"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
          <div className="text-end mt-1">
            <a href="/forgot-password" className="text-decoration-none">
              
            </a>
          </div>
        </div>
        <div className="mb-3" style={{ maxWidth: '400px', margin: '0 auto' }}>
          <label className="form-label"></label>
          <input
            type="password"
            className="form-control"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        <div className="mb-3" style={{ maxWidth: '400px', margin: '0 auto' }}>
          <a href="/register" className="text-decoration-none">
            
          </a>
        </div>
        <div style={{ maxWidth: '400px', margin: '0 auto' }}>
          <button type="submit" className="btn btn-warning w-100"></button>
        </div>
      </form>
    </div>
  );
}
