import React from 'react';
import { Container, Row, Col, Card, Nav, Navbar, NavDropdown } from 'react-bootstrap';
import { Bar } from 'react-chartjs-2';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';
import './Dashboard.css';
import Sidebar from "./Sidebar";
import {useNavigate} from "react-router-dom";
import { toast } from 'react-toastify';
import axios from 'axios';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const data = {
  labels: ['10', '10', '20', '30', '40', '50', '60', '70', '80', '90'],
  datasets: [
    {
      label: '',
      backgroundColor: '#ffa726',
      data: [300, 450, 680, 810, 900, 700, 400, 200, 100, 50]
    },
    {
      label: '',
      backgroundColor: '#fb8c00',
      data: [250, 400, 600, 780, 850, 650, 350, 180, 90, 40]
    }
  ]
};

const options = {
  responsive: true,
  plugins: {
    legend: {
      position: 'top'
    },
    title: {
      display: true,
      text: ''
    }
  }
};


const Dashboard = () => {
  const navigate=useNavigate();
  const handleLogout = async () => {
    try {
      const token = localStorage.getItem('token');
      await axios.post('https://backend-userlist-7.onrender.com/user/logout', {}, {
        headers: {
          Authorization: `Bearer ${token}`,
        }
      });
      localStorage.removeItem('token');
      toast.success('');
      navigate('/login');
    } catch (error) {
      toast.error('');
    }
  };
  
  return (
    <>
      <Navbar className="navbar-custom" expand="lg" fixed="top">
        <Container fluid>
          <Navbar.Brand href="/"></Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ms-auto">
              <NavDropdown
                title={<i className="fas fa-user-circle fa-2x"></i>} // Profile icon
                id="profile-dropdown"
                align="end"
              >
                <NavDropdown.Item href="/profile"></NavDropdown.Item>
                <NavDropdown.Item href="/settings"></NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item onClick={handleLogout}></NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Container fluid className="p-0">
        <Row className="g-0">
          <Sidebar />
          <Col md={10} className="p-4 main-content">
            <Row>
              {/* Top Row - 3 Cards Left to Right */}
              <Col md={4}>
                <Card className="mb-4 shadow-lg custom-card">
                  <Card.Body>
                    <Card.Title></Card.Title>
                    <h1>450</h1>
                    <Card.Text>400 ()</Card.Text>
                    <small className="text-success"> 12.5%</small>
                  </Card.Body>
                </Card>
              </Col>
              <Col md={4}>
                <Card className="mb-4 shadow-lg custom-card">
                  <Card.Body>
                    <Card.Title></Card.Title>
                    <h1>50 / </h1>
                    <Card.Text>12 ()</Card.Text>
                    <small className="text-success"> 316.6%</small>
                  </Card.Body>
                </Card>
              </Col>
              <Col md={4}>
                <Card className="mb-4 shadow-lg custom-card">
                  <Card.Body>
                    <Card.Title></Card.Title>
                    <h1>10% / </h1>
                    <Card.Text>12% ()</Card.Text>
                    <small className="text-danger"> 16.6%</small>
                  </Card.Body>
                </Card>
              </Col>
            </Row>

            <Row>
              
              <Col md={8}>
                <Card className="shadow-lg custom-card">
                  <Card.Body>
                    <Card.Title></Card.Title>
                    <Bar data={data} options={options} />
                  </Card.Body>
                </Card>
              </Col>
              <Col md={4}>
                <Card className="mb-4 shadow-lg custom-card">
                  <Card.Body>
                    <Card.Title></Card.Title>
                    <h1>125 / </h1>
                    <Card.Text>85 ()</Card.Text>
                    <small className="text-success"> 47%</small>
                  </Card.Body>
                </Card>
                <Card className="mb-4 shadow-lg custom-card">
                  <Card.Body>
                    <Card.Title></Card.Title>
                    <h1>10 / </h1>
                    <Card.Text>8 ()</Card.Text>
                    <small className="text-success"> 25%</small>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default Dashboard;
